import React, {Component } from 'react'

class Nav extends Component {

    constructor(props) {
        super(props)

        this.state = {
            'searchItem' : '',
            'zipCode' :''
        }

        this.updateZipCode = this.updateZipCode.bind(this);
        this.searchVenues = this.searchVenues.bind(this);

    }

    searchVenues() {

        // this.setState({
        //     'searchItem' :'newSearch'
        // });



    }

    updateZipCode(event) {

        this.setState({
            'zipCode' :event.target.value
        });
    }

    render(){

        return <div>
                <input onChange={this.updateZipCode} type="text" name='zipCode' value={this.state.zipCode} placeholder="Zip Code" />
                <div>{this.state.searchItem} -- {this.state.zipCode}</div>
                <button onClick={this.searchVenues}>Search</button>
            </div>
    }

}

export default Nav;
